import Vue from 'vue'
import VueRouter from 'vue-router'
// import Home from '../views/Home.vue'
import Blog from '../views/Blog.vue'
import Shine from '../views/ShineButton/ShineButton.vue'
import ImageShadow from '../views/ImageShadow/ImageShadow.vue'
import CursorEffect from '../views/CursorEffect/CursorEffect.vue'
import WaterEffect from '../views/WaterEffect/WaterEffect.vue'
import PhotoShop from '../views/PhotoShop/PhotoShop.vue'
import Flip from '../views/Flip/Flip.vue'
import Pile from '../views/Pile/Pile.vue'
import Text from '../views/Text/Text.vue'
import Pixi from '../views/Pixi/Pixi.vue'
import Maze from '../views/Maze/Maze.vue'
import Nebulon from '../views/nebulon/nebulon.vue'
import Clock from '../views/Clock/Clock.vue'
import City from '../views/Clock/City.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Blog
  },
  {
    path: '/shine',
    name: 'Shine Button',
    component: Shine
  },
  {
    path: '/imgShadow',
    name: 'Image Shadow',
    component: ImageShadow
  },
  {
    path: '/cursorEffect',
    name: 'Cursor Effect',
    component: CursorEffect
  },
  {
    path: '/waterEffect',
    name: 'Water Effect',
    component: WaterEffect
  },
  {
    path: '/photoShop',
    name: 'PhotoShop',
    component: PhotoShop
  },
  {
    path: '/flip',
    name: 'Flip Clock',
    component: Flip
  },
  {
    path: '/pile',
    name: 'Text Pile and Picture Pile',
    component: Pile
  },
  {
    path: '/text',
    name: 'Text Show and Hide Effect',
    component: Text
  },
  {
    path: '/pixi',
    name: 'Pixi Tutorial',
    component: Pixi
  },
  {
    path: '/maze',
    name: 'Maze Game',
    component: Maze
  },
  {
    path: '/nebulon',
    name: 'Nebulon',
    component: Nebulon
  },
  {
    path: '/clock',
    name: 'City Clock',
    component: Clock
  },
  {
    path: '/city/:id',
    name: 'city',
    component: City
  }
]

const router = new VueRouter({
  routes
})

export default router
