














import Cities from './heritage_data'
import {Vue, Component} from 'vue-property-decorator';

@Component({})
export default class Clock extends Vue{
  private cities: any[] = Cities
  private city: any
  private times: string[] = []
  private imgs: string[] = []
  private created(){
    this.city = this.cities.find( item => item.id === this.$route.params.id )
    for( let key in this.city.fp ){
      this.times[Number(key)] = this.city.fp[key].slice(0, 2) + ':' + this.city.fp[key].slice(2)
      this.imgs[Number(key)] = `https://www.sony.net/united/clock/share/img/photo/${this.city.id}/fp_hd/${key}.jpg`
      const img = new Image()
      img.src = this.imgs[Number(key)]
    }
    this.times.shift()
    this.imgs.shift()
    this.imgs.reverse()
  }
  private roll($event: MouseWheelEvent){
    // 控制滚轮精度
    (document.querySelector('#app') as HTMLElement).scrollTop += $event.deltaY > 0 ? 40 : -40
    const hight = window.innerHeight
    const scroll = (document.querySelector('#app') as HTMLElement).scrollTop
    // compute
    const per = (scroll % hight) / hight
    const index = Math.floor(scroll / hight)
    document.querySelectorAll('.photo')[11 - index].style.opacity = 1 - per < 0.05 ? 0: 1 - per
    console.log($event)
    $event.preventDefault()
    
  } 
}
